import React from "react"
import { Link } from "gatsby"

const PostLinks = ({ prev, next }) => (
  <div className="post-links">
    <div className="next">
      {next && (
        <Link to={next.frontmatter.path}>
          ← <span>Next Post</span>
        </Link>
      )}
    </div>
    <div className="prev">
      {prev && (
        <Link to={prev.frontmatter.path}>
          <span>Prev Post</span> →
        </Link>
      )}
    </div>
  </div>
)

export default PostLinks
