import React from "react"
import { graphql, Link } from "gatsby"
import Layout from "../../components/Layout"
import PostLinks from "../../components/PostLinks"

const BlogTemplate = ({ data, pageContext }) => {
  const { markdownRemark } = data
  const { frontmatter, html } = markdownRemark
  const { prev, next } = pageContext

  return (
    <Layout>
      <main className="post">
        <header className="post-header">
          <div className="title-content">
            <h1>{frontmatter.title}</h1>
            <blockquote className="phrase">
              <cite>
                <q>{frontmatter.phrase}</q>
              </cite>
              <p className="phrase-author">~ {frontmatter.author} ~</p>
            </blockquote>
            <strong>{frontmatter.date}</strong>
          </div>
        </header>
        <section
          className="post-content"
          dangerouslySetInnerHTML={{ __html: html }}
        />
        <section>
          <PostLinks prev={prev} next={next} />
          <PostMoreInfo />
          <Comment />
        </section>
      </main>
    </Layout>
  )
}

const PostMoreInfo = () => (
  <div className="post-more-info">
    <div>
      <h3>Related Posts</h3>
    </div>
    <div className="all">
      <Link className="all-button" to="/">
        View All Posts
      </Link>
    </div>
  </div>
)

const Comment = () => (
  <div className="comment">
    <div>
      <h3>Leave a comment</h3>
    </div>
    <div>
      <h3># Coment(s)</h3>
    </div>
  </div>
)

export const query = graphql`
  query($path: String!) {
    markdownRemark(frontmatter: { path: { eq: $path } }) {
      html
      frontmatter {
        date(formatString: "MMMM DD, YYYY")
        path
        title
        image {
          childImageSharp {
            fluid(maxWidth: 1200) {
              ...GatsbyImageSharpFluid
            }
          }
        }
        tags
        phrase
        author
      }
    }
  }
`

export default BlogTemplate
